import React from 'react';
import Link from './img/external-link.svg';
import PropTypes from 'prop-types';

const HeroBlock = (props) => {
    const heroContent = props.heroContent;

    return (
        <div>
            <h4>{heroContent.subTitle}</h4>
            <h2>{heroContent.title}</h2>
            {heroContent.text.toString() !== '' ? <p>{heroContent.text}</p> : null}
            {props.expand ? <div className="info-link">
                        <h6>Consultant at:</h6>
                        <a href="https://movify.be/"><span>Movify</span><img src={Link}/></a>
                    </div> : ""}
        </div>
    );
}

HeroBlock.defaultProps = {
    heroContent: {},
    expand: false,
}

export default HeroBlock;