import React, {Component} from 'react';
import Menu from './img/menu.svg';
import X from './img/x.svg';
import Download from './img/download.svg'
import { NavLink } from 'react-router-dom';
import { menuItems } from '../data/Data';

class Header extends Component {

    state = {
        visible: false,
        menuItems: menuItems,
    }

    toggleMenu = () => {
        this.setState( prevState => ({ 
            visible: !prevState.visible,
        }))
    }

    render() {

        const isVisible = this.state.visible;
        return (
            <nav>
                <div className="nav-upper">
                    <h1><NavLink to="/">Simon Vanleeuw</NavLink></h1>
                    <img id="menu-button" onClick={this.toggleMenu} src={isVisible ? X : Menu}/>
                    <div id="menu" className={isVisible ? "visible" : "hidden"}>
                        <ul>
                            {/* {this.state.menuItems.map(menuItem => {
                                return (
                                    <li key={menuItem.id}><NavLink to={menuItem.path}>{menuItem.value}</NavLink></li>
                                    )
                            })} */}
                            <li className={isVisible ? "pop-in" : "pop-out"}><NavLink to="/work">Work</NavLink></li>
                            <li className={isVisible ? "pop-in" : "pop-out"}><NavLink to="/about">About</NavLink></li>
                            <button className={`secondary ${isVisible ? "pop-in" : "pop-out"}`}><span>CV</span><img src={Download}/></button>
                            <button className={`primary ${isVisible ? "pop-in" : "pop-out"}`}>Get in touch</button>
                        </ul>
                    </div>
                </div>
            </nav>
        )
    }
}

export default Header;